import Link from "next/link";
import { Box, Image } from "rebass";
import { isAbsolute } from "path";
import { newbase } from "../styles/colors.json"; 

const links = [
    {
      href: "https://facebook.com/hackrice",
      label: "Facebook",
      icon: "https://icon.now.sh/facebook/ffffff"
    },
    {
      href: "https://medium.com/@hackrice",
      label: "Medium",
      icon: "https://icon.now.sh/medium/ffffff"
    },
    {
      href: "https://twitter.com/hackingrice",
      label: "Twitter",
      icon: "https://icon.now.sh/twitter/ffffff"
    },
    {
      href: "mailto:officialhackrice@gmail.com",
      label: `Email`,
      icon: "https://icon.now.sh/email/ffffff"
    },
    {
      href: "https://hackrice8.devpost.com/",
      label: "Devpost",
      icon: "https://icon.now.sh/build/ffffff"
    }
  ].map(link => {
    link.key = `nav-link-${link.href}-${link.label}`;
    return link;
  });

const NewIntro = () => (
    <Box bg={newbase} className="contain">
        <Image
            src="../static/hr85.png"
            css={{
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                marginTop: "15%",
                width: "50%"
            }}
            /> 
        <div className="buttons">
          <Link href="/8"><Image className="special" width={1/4} src="../static/btn.svg" /></Link>
          <Link href="https://goo.gl/forms/0p6Jf974trcf1Qc92"><Image className="special" width={1/6} src="../static/btn2.svg" /></Link>
        </div>
        <div className="social">
        {links.map(({ key, href, label, icon }) => (
            <Link href={href} key={key}>
                <a>
                <img src={icon} className="icon" />
                </a>
            </Link>
        ))}
        </div>
        <style jsx global>{`
            .buttons {
              display: flex;
              flex-flow: column wrap;
              align-items: center;
              height: 150px;
              justify-content: space-around;
              margin-top: 3em;
            }

            img.special { cursor: pointer; }

            @media only screen and (max-width: 770px) {
                .special, .special2 {
                    font-size: 1em !important;
                }
            }
            
            .icon {
                padding: 4px 13px;
            }

            .social {
                text-align: center;
                margin-top: 10em;
            }
        `}</style>
    </Box>
);

export default NewIntro;


